<script>
export default {
  data() {
    return {
      addDialog: true,
      loading: false,
      name: ''
    }
  },
  methods: {
    closeAddModal() {
      this.addDialog = false;
      this.$router.go(-1);
    },
    saveStudent() {
      this.loading = true;
      this.$store.dispatch('addStudent', {
        name: this.name,
        groupId: this.$route.params.groupId || null
      }).then(() => {
        this.loading = false;
        this.closeAddModal();
      })
    }
  }
}
</script>

<template>
  <v-dialog
      v-model="addDialog"
      max-width="500px"
  >
    <v-card>
      <v-card-title>
        <span class="headline">Add Student</span>
      </v-card-title>
      <v-card-text>
        <v-form>
          <v-text-field
              label="Name"
              required
              v-model="name"
              :disabled="loading"
          ></v-text-field>
        </v-form>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
            color="blue darken-1"
            text
            @click="closeAddModal"
        >Cancel
        </v-btn>
        <v-btn
            color="blue darken-1"
            text
            @click="saveStudent"
            :loading="loading"
        >Save
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

